<template>
  <div class="main">
    <div class="row hot" v-if="Data">
      <div class="container coursePageAll">
        <div class="coursePageTitle">
          <h3>教學資源</h3>
        </div>
        <div class="t_page">
          <div class="breadcrumb">
            <ul>
              <li><router-link to="/techplans">教學資源</router-link></li>
              <li>
                <router-link :to="`/techplans/${Data.TeachPlanCategoryID}`">{{
                  Data.Category.Name
                }}</router-link>
              </li>
            </ul>
          </div>
          <div class="tableStyle1">
            <table>
              <tr>
                <th colspan="2">{{ Data.Category.Name }}</th>
              </tr>
              <tr>
                <td>教案名稱</td>
                <td>{{ Data.Name }}</td>
              </tr>
              <tr>
                <td>設計者</td>
                <td>{{ Data.Teacher }}</td>
              </tr>
              <tr>
                <td>教學領域</td>
                <td>{{ Data.Category.Name }}</td>
              </tr>
              <tr>
                <td>適用對象</td>
                <td>{{ Data.Domain }}</td>
              </tr>
              <tr>
                <td>欣賞影片</td>
                <td>{{ Data.VideoName }}</td>
              </tr>
              <tr>
                <td>教學目標</td>
                <td v-html="Data.Goal"></td>
              </tr>
              <tr>
                <td>教案下載</td>
                <td v-if="Data.TeachPlanUrl">
                  <a
                    class="downloadBtn"
                    :href="Data.TeachPlanUrl | imageCDN"
                    target="_blank"
                    ><img src="/img/download.svg" />下載</a
                  >
                </td>
                <td v-else>無</td>
              </tr>
              <tr>
                <td>學習單下載</td>
                <td v-if="Data.LearnSheetUrl">
                  <a
                    class="downloadBtn"
                    :href="Data.LearnSheetUrl | imageCDN"
                    target="_blank"
                    ><img src="/img/download.svg" />下載</a
                  >
                </td>
                <td v-else>無</td>
              </tr>
              <tr v-if="false">
                <td>教學示範影片觀看</td>
                <td v-if="Data.VideoUrl">
                  <video :src="Data.VideoUrl | imageCDN" controls></video>
                </td>
                <td v-else>無</td>
              </tr>
              <tr>
                <td>線上師培課程</td>
                <td v-if="Data.Lesson">
                  <router-link :to="'/lesson/' + Data.Lesson.LessonID">{{
                    Data.Lesson.LessonName
                  }}</router-link>
                </td>
                <td v-else>無</td>
              </tr>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      Data: null,
    };
  },
  async mounted() {
    var [category, response] = await Promise.all([
      this.$api.getTeachPlanCategories(),
      this.$api.getTeachPlan(this.$route.params.id),
    ]);
    this.Categories = category;
    var map = {};
    category.forEach((c) => (map[c.TeachPlanCategoryID] = c));
    var plan = response.TeachPlan;
    plan.Lesson = response.Lesson;
    plan.Category = map[plan.TeachPlanCategoryID];
    this.Data = plan;
  }
};
</script>